<template>
  <div>
    <pageBanner :banner-title="this.$route.meta.title" :bannerPath="this.$route.path"></pageBanner>
    <section class="blog-wrap-layout21">
      <div class="container">
        <div class="row gutters-40">
          <div class="col-xl-3 col-sm-6 col-12" v-for="(item,index) in articleList" :key="index" >
            <div class="blog-box-layout9" style="background-color: #ebebeb;border-radius: 15px 15px 5px 5px">
              <router-link :to="{name:'homeArticle',params:item}" >

              <div class="item-img" style="border-radius: 15px 15px 5px 5px"><a>
                <img v-lazy="item.logo" alt="blog" >
              </a></div>
              <div class="item-content" >
                <h3 class="item-title" style="font-size: 14px;margin-bottom: 5px;text-align: left"><div v-html="HTMLDecode(item.title)"></div></h3>
                <ul class="entry-meta meta-color-dark" style="text-align: left">
                  <li><i class="fas fa-tag"></i>{{ item.description }}</li>
                  <li><i class="far fa-clock"></i>{{ getDateDiff(item.time) }}</li>
                </ul>
                <h3 class="item-title" style="font-size: 14px"><div v-html="HTMLDecode(item.introduction)" class="text-box1"></div></h3>

              </div>
              </router-link>
            </div>
          </div>

        </div>
        <div class="loadmore-btn-layout1" @click="loadMore"><a href="#" class="item-btn">LOAD MORE</a></div>

      </div>
    </section><!-- Blog Area End Here --><!-- Blog Area Start Here -->

  </div>
</template>

<script>
import {codeProductList} from "@/api/codeProducts";
import {getDateDiff, HTMLDecode} from "@/utils/other/commonFun";
import pageBanner from "@/views/common/pageBanner"
export default {
  name: "Cate",
  data() {
    return {
      page: 1,
      pages: 1,
      articleList: [],
      loading: true,
    }
  },
  components:{
    pageBanner:pageBanner
  },
  mounted() {
    this.getCodeProductList()
    console.log(this.$route)
  },
  watch:{
    '$route.params':{
      immediate:true,
      // deep:true,
      handler(val){
        console.log(val)
        this.getCodeProductList()
      }
    }
  },
  
  methods: {
    getCodeProductList(page = null) {
      if (!page) page = this.page
      let that = this
      codeProductList({page: page, cate_id: this.$route.params.id}).then(res => {
        that.page = res.page
        that.pages = res.pages
        that.articleList = res.list
      })
    },
    HTMLDecode(str) {
      return HTMLDecode(str)
    },
    getDateDiff(str) {
      return getDateDiff(str)
    },
    loadMore() {
      if (this.page >= this.pages) {
        this.$message('没有更多文章了!')
        return
      }
      if (!this.loading) {
        this.$message('你太快了!')
        return
      }
      this.loading = false
      this.getCodeProductList(this.page + 1)
      this.loading = true

    }

  }
}
</script>

<style scoped>
.text-box1 >>> p{
  float: left;
  width: 100%;
  line-height: 25px!important;
  /*display: -webkit-box;*/
  text-overflow: ellipsis;
  overflow: hidden;
  -webkit-box-orient:vertical;
  -webkit-line-clamp:2;
}
</style>